<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container mb-4 p-2">
      <div class="row products d-flex flex-wrap justify-content-between align-items-start my-4 p-2">
        <div
          id="display"
          class="table col-md-4 col-sm-6 d-flex justify-content-between align-items-center flex-wrap"
          v-for="(item) in filterProducts"
          :key="item.id"
        >
          <div class="card my-3 mx-3 trans d-flex shadow-sm rounded" :data-id="item.id"  style="width: 25rem;">
            <!-- 前往單一產品頁 -->
            <router-link :to="`/product/${item.id}`">
              <div class="imgbox ">
                <img :src="item.imageUrl" class="card-img-top" alt />
                <!-- <img :src="item.imageUrl" class="card-img-top" v-show="trans" alt /> -->
              </div>
            </router-link>
            <!-- card body -->
            <div class="mt-3 p-2  ">
              <div class="card-text text-center">{{ item.title }}</div>
              <div class="card-body p-2 d-flex justify-content-center">
                <div class="price_box d-flex justify-content-center align-items-center">
                  <!-- 多一個div 隔開兩種price -->
                  <div class="d-flex justify-content-center align-items-center">
                    <!-- <small class="mr-1 text-muted">{{ item.origin_price}}</small> -->
                    <span class="font-weight-bold">$ {{ item.price}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-12 d-flex justify-content-center">
        <pagination :pages="pagination" class="my-4" @emit-pages="getProducts"></pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'
export default {
  name: 'Products',
  data () {
    return {
      products: [],
      isLoading: false,
      filterType: '',
      filterProducts: [],
      pagination: {},
      cart: {},
      uuid: process.env.VUE_APP_UUID
    }
  },
  components: {
    Pagination
  },
  created () {
    this.getProducts()
  },
  methods: {
    getProducts (pages = 1) {
      this.isLoading = true
      this.$http
        .get(
          `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/products?page=${pages}`
        )
        .then((res) => {
          this.isLoading = false
          this.products = res.data.data
          this.filterProducts = this.products
          this.pagination = res.data.meta.pagination
        })
        .catch((error) => {
          console.log(error)
        })
    },
    addCart (id, quantity = 1) {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/shopping`
      const cart = {
        product: id,
        quantity
      }

      this.axios
        .post(url, cart)
        .then(() => {
          this.isLoading = false
          this.$bus.$emit('update-Total')
          this.$bus.$emit('Message:push', '成功加入購物車', 'success')
        })
        .catch((err) => {
          const error = err.response.data.errors
          console.log('123')
          error.forEach((error) => {
            this.isLoading = false
            this.$bus.$emit(
              'Message:push',
              '加入購物車失敗',
              `${error}`,
              'danger'
            )
          })
        })
    }
  },
  addtrans () {
    document.querySelector('.card').classList.add('trans')
  }
}
</script>
